@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in-up {
  animation: fade-in-up 0.5s ease-out;
}

/* NOTE: TextRevealLines builds its own wrappers + animates inner spans via GSAP.
   Avoid global `.line` styling here to prevent layout/overlap issues. */
